.account-card {
  color: #414042;
  margin: 10px 20px 10px 0;
  width: 200px;
  height: 200px;
  border-radius: 10px;
  border: none;
  box-shadow: 4px 2px 12px 9px rgba(0, 0, 0, 0.02);
  background-color: #ffffff;
  cursor: pointer;
}
.account-card .flag {
  display: block;
  width: 30px;
  height: 20px;
  float: left;
}
.account-card .GBP {
  background: url("../../assets/images/gbp.png") 0 0;
}
.account-card .EUR {
  background: url("../../assets/images/eur.png") 0 0;
}
.account-card .USD {
  background: url("../../assets/images/usd.png") 0 0;
}
.account-card .HKD {
  background: url("../../assets/images/hkd.png") 0 0;
}
.account-card .currency {
  font-size: 14px;
  float: left;
  display: block;
  margin-left: 10px;
}
.account-card .amount {
  font-family: "Carnas-Medium";
  font-size: 27px;
  clear: both;
  display: block;
  margin-top: 10px;
}
.account-card .account-link {
  color: #414042;
  font-size: 14px;
  display: block;
  text-decoration: none;
  position: relative;
}
.account-card .account-link:hover,
.account-link:active {
  color: #414042;
}
.account-card .account-link::after {
  font-family: "Carnas-Light";
  position: absolute;
  font-size: 45px;
  content: "â€º";
  right: 0px;
  top: -18px;
  color: #414042;
}
.account-card .account-link .account-type {
  font-size: 12px;
  display: block;
  color: #989898;
}
