@font-face {
  font-family: "Carnas-Thin";
  src: url("assets/fonts/CarnasOT/Carnas-Thin.otf");
}
@font-face {
  font-family: "Carnas-Medium";
  src: url("assets/fonts/CarnasOT/Carnas-Med.otf");
}
@font-face {
  font-family: "Carnas-Regular";
  src: url("assets/fonts/CarnasOT/Carnas-Reg.otf");
}
@font-face {
  font-family: "Carnas-Bold";
  src: url("assets/fonts/CarnasOT/Carnas-Bold.otf");
}
@font-face {
  font-family: "Carnas-Light";
  src: url("assets/fonts/CarnasOT/Carnas-Lig.otf");
}
body {
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#f9f9f9+0,ffffff+40,f9f9f9+100 */
  background: #f9f9f9; /* Old browsers */
  background: -moz-linear-gradient(
    left,
    #f9f9f9 0%,
    #ffffff 40%,
    #ffffff 100%
  ); /* FF3.6-15 */
  background: -webkit-linear-gradient(
    left,
    #f9f9f9 0%,
    #ffffff 40%,
    #ffffff 100%
  ); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to right,
    #f9f9f9 0%,
    #ffffff 40%,
    #ffffff 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f9f9f9', endColorstr='#ffffff',GradientType=1 ); /* IE6-9 */
  margin: 0;
  font-family: "Carnas-Regular";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 100%;
  color: #ababab;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
