.liquify-nav {
  color: #414042;
  box-sizing: border-box;
  padding: 18px 0;
  border: solid 1px #e0e0e0;
  box-shadow: 4px 10px 12px 9px rgba(0, 0, 0, 0.06);
  background-color: #414042;
  margin: 0 -15px;
}
.liquify-nav .nav-logo img {
  height: 28px;
  margin-top: 5px;
}
.liquify-nav .nav .nav-item .nav-link {
  font-size: 14px;
  color: #ffffff;
}
.liquify-nav .nav .nav-item.bell-border {
  border-left: 1px solid #6c6c6c;
  border-right: 1px solid #6c6c6c;
}
.liquify-nav .nav .nav-item .fa-bell {
  color: #f8df2f;
}
.liquify-nav .nav .nav-item .bell-container {
  position: relative;
  padding-right: 20px;
}

.liquify-nav .nav .nav-item .bell-container .fa-bell {
  color: #f8df2f;
}
.liquify-nav .nav .nav-item .bell-container .badge-count {
  display: block;
  position: absolute;
  top: 0px;
  right: 5px;
  height: 20px;
  width: 20px;
  background-color: #db0011;
  border-radius: 50%;
  text-align: center;
  font-size: 12px;
  border: solid 1px #1d262c;
}
.liquify-nav .nav .nav-item .nav-link .fa-user-circle {
  margin-left: 5px;
}
