.login-wrapper {
  border-radius: 10px;
  box-shadow: 4px 2px 12px 9px rgba(0, 0, 0, 0.04);
  background-color: #ffffff;
  margin: 50px auto;
}
.login-left-col {
  width: 460px;
  height: 560px;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  background-color: #fafafa;
  vertical-align: middle;
  white-space: nowrap;
  text-align: center;
}
.login-left-col img {
  vertical-align: middle;
}
.helper {
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}
.login-right-col {
  margin: 65px 60px 0 60px;
}
.logo {
  background: url("../assets/images/LIQUIFY-logo.png") 0 0;
  height: 30px;
  width: 143px;
  background-size: cover;
}
.logo-title {
  font-family: "Carnas-Light";
  margin-top: 15px;
  color: #414042;
  font-weight: 300;
}
.login-form {
  margin-top: 3rem;
}
.login-form-heading {
  font-family: "Carnas-Medium";
  color: #414042;
}
.login-form-input {
  padding: 36px 20px 28px;
  font-size: 14px;
}
.login-form-text-main-err {
  padding-left: 0px !important;
}
.login-form-input-err {
  border: solid 1px #ca0000;
  background-color: #ffeded;
}
.login-form-text-err {
  color: #ca0000;
  padding-left: 20px;
  margin-top: 5px;
  display: inline-block;
}
.login-btn {
  font-family: "Carnas-Medium";
  font-size: 14px;
  padding: 10px 50px;
  letter-spacing: 0.9px;
  color: #414042;
}
.login-button {
  border: solid 1px #333333;
  background-color: transparent;
  cursor: disabled;
  opacity: 0.2;
}
.login-button-active {
  border: solid 1px #f8df2f;
  background-color: #f8df2f;
}
.forgot-login,
.forgot-login:hover,
.forgot-login:active {
  display: inline-block;
  color: #ababab;
  margin-top: 20px;
}

.login-form-control-wrapper {
  position: relative;
  display: block;
}
.login-form-control-lable {
  position: absolute;
  top: 20px;
  left: 20px;
  transition: all 0.3s linear;
}
.login-form-control-lable-hidden {
  top: 22px;
}
.login-form-control-lable-active {
  top: 5px;
}

.copy {
  font-family: "Carnas-Light";
  font-size: 11px;
  position: absolute;
  bottom: 20px;
  color: #414042;
}
